import { Grid } from './grid';
function _between(value, lhs, rhs) {
    return lhs <= value && value <= rhs;
}
function isGridDimensionEvent(event) {
    return event.type == 'dimension';
}
function isGridBlockEvent(event) {
    return event.type == 'block';
}
function isGridCanvasEvent(event) {
    return event.type == 'canvas';
}
window.between = _between;
window.Grid = Grid;
window.isGridDimensionEvent = isGridDimensionEvent;
window.isGridBlockEvent = isGridBlockEvent;
window.isGridCanvasEvent = isGridCanvasEvent;
